.container {
  width: 100%;
  max-width: stretch;
  max-height: stretch;
  border-radius: 20px;
  border: 5px groove #3e645d;
  background-color: #002c30;
  background-image: url(../../assets/images/textures/gplay.png);
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  justify-content: center;
  overflow-y: auto;
  box-shadow: 0px 0px 5px 3px black inset;
}
